

.net {
    background-color: black;
    display: flex;
    flex-direction: row;
    position: fixed;
    top: 0;
    width: 100%;
}

.net h1 {
    color: red;
    font-size: 22px;
    margin-left: 10px;
    letter-spacing: 1px;
}

.netBox {
    display: flex;
    flex-direction: row;
    position: absolute;
    right: 5%;
}

.netBox h4 {
    color: white;
    font-size: 15px;
    margin-right: 4px;
}

.netBox button {
    text-decoration: none;
    border: none;
    margin: auto;
    margin-right: 4px;
    margin-left: 4px;
    width: 90px;
    height: 30px;
    border-radius: 3px;
}

#join {
    background-color: red;
    color: white;
    padding: 5px;
}

#sign {
    background-color: black;
    color: white;
    padding: 5px;
    border: 1px solid white;
}