.cardbox {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: none;
    margin: 10px;
}

a {
    list-style: none;
    text-decoration: none;
    color: red;
    font-size: 18px;
    margin-top: 10px;
    border: 3px solid gray;
    border-radius: 4px;
    width: 100px;
    height: 30px;
    text-align: center;
    padding-top: 8px;
    background-color: cyan;
}
a:hover{
    background-color: blue;
    color: red;
    border: 3px solid darkturquoise;
}

figcaption{
    color:orange;
    border: 2px solid lightcoral;
    width: 200px;
    height: 38px;
    background-color: azure;
    border-radius: 5px;
    margin-top: 12px;
    box-shadow: 5px 5px 3px;
    text-align: center;
    padding-top: 10px
}

img{
    border: 3px solid red;
    border-radius: 10px;
}

img:hover{
    -ms-transform: scale(1.5); 
    -webkit-transform: scale(1.5); 
    transform: scale(1.5);
}